import { useState, useEffect } from 'react';
import { CloudUrl } from '../../../constants';
import { isSocialEmbed, isSocialWall } from '../../../actions/api';
import './Upgrade.scss';


const UpgradePopUp = ({ appData }) => {
    const { wall } = appData;
    const [isUpgrade, setIsUpgrade] = useState(false);

    useEffect(() => {
        let upgradeTimeout;
        let brandingTimeout;

        const updateIsUpgrade = () => {
            upgradeTimeout = setTimeout(() => {
                setIsUpgrade(true);
                upgradeScreenTimer();
            }, 30000);
        };

        const upgradeScreenTimer = () => {
            if (wall.UserRule.branding) {
                brandingTimeout = setTimeout(() => {
                    setIsUpgrade(false);
                    updateIsUpgrade();
                }, 15000);
            }
        };

       
        updateIsUpgrade();

        return () => {
            clearTimeout(upgradeTimeout);
            clearTimeout(brandingTimeout);
        };
    }, [wall.UserRule.branding]);


    return <><div className={`tb_ug_m ${isUpgrade ? 'tb_show_' : 'tb_hide_'}`}>
        <div className="tb_ug_m_w">
            <div className="tb_ug_body">
                <div className="tb_ug_img tb_ug_img__" style={{ backgroundImage: `url(${CloudUrl}/media/error/upgrade-display.svg)` }}>
                </div>
                <div className="tb_ug_title">Want To Get Past This Pop-up ?</div>
                <div className="tb_ug_s_title">
                    <p> {isSocialEmbed ? `To enhance your website experience, re-activate your Social wall now!` : `Upgrade Your Plan Now & Personalize Your Social Wall By Eliminating The Social Wall Branding!`}</p>
                </div>
                <div className="tb_ug_btn_w">
                    <a target="_blank" className="tb_ug_btn" href={isSocialWall ? "https://socialwalls.com/price/" : "https://taggbox.com/price/display-price/"}>Upgrade Now!</a>
                </div>
            </div>
        </div>
    </div>
        <div className={`tb_ug_overlay__ ${isUpgrade ? 'tb_show_' : ''} `}></div>
    </>
};

export default UpgradePopUp;
